  getJsonl(path){
    let url = serverUrl + path + ".jsonl";
    axios.get(url)
    .then(response => {
      let jsonArray = [response.data];
      if(typeof response.data === 'string'){
        jsonArray = parseJsonl(response.data);
      }
      if(path == "data/stats"){
        jsonArray.sort(this.sortByProperty("totalmatchcount"));
        this.state.stats = jsonArray;
        for(var i = 0; i < this.state.stats.length; i++){
          this.calculatePlayerStats(this.state.stats[i]);
        }
      }
      if(path == "data/replayInfo"){
        jsonArray.sort(this.sortByProperty("id"));
        this.state.replayInfo = jsonArray;
      }
      if(path.includes("chatlogs/")){
        this.state.chatLog = jsonArray;
      }
      //FORCE UPDATE STATE
      this.setState({curlink: this.state.curlink})
    });
  }



  msToString(ms){
    var totalSeconds =  Math.floor(ms / 1000);
    var hours = Math.floor(totalSeconds / 3600);
    var minutes = Math.floor((totalSeconds - (hours*3600)) / 60);
    var seconds = totalSeconds - (hours*3600) - (minutes * 60);
    var timeString = hours + "h" + minutes + "m" + seconds + "s";
    return timeString;
  }


  sortByProperty(property){
     return function(a,b){
       let aF = parseFloat(a[property]);
       let bF = parseFloat(b[property]);
        if(aF < bF)
           return 1;
        else if(aF > bF)
           return -1;

        return 0;
     }
  }

