  loadHeatmap(auth, i){
    let url = serverUrl + "heatmaps/players/" + auth + ".json";
    axios.get(url)
    .then(response => {
      let jsonArray = [response.data];

      jsonArray = jsonArray[0];


      let data = {
        heatmap: [],
        visible: true
      };
      let maxValue = 1;
      let totalValue = 0;
      for(var x = 0; x < jsonArray.length; x++){
        for(var y = 0; y < jsonArray[x].length; y++){
          let xCoord = x * 6.5;
          let yCoord = y * 3.7666;
          let dataPoint = {
            x: xCoord,
            y: yCoord,
            value: jsonArray[x][y]+1
            //value: Math.min(jsonArray[i].heatmap[x][y] +1, 3)
          }
          data.heatmap.push(dataPoint);
          totalValue += dataPoint.value;
          if(dataPoint.value > maxValue){
            maxValue = dataPoint.value;
          }
        }
      }

      this.state.playerHeatmaps[i] = data;
      //console.log("MAXVALUE: " + maxValue);
      maxValue = (totalValue / (jsonArray.length * jsonArray[0].length)) * 3;
      //console.log("MAXVALUE: " + maxValue);
      this.state.playerHeatmapsMaxvalue[i] = maxValue;
      this.setState({playerHeatmaps: this.state.playerHeatmaps});
      //console.log(data);


    });
  }

  generateHeatMap(auth, index){
    let heatmap = this.state.playerHeatmaps[index].heatmap;
    //console.log("GENERATING HEATMAP FOR " + auth);
    //console.log(heatmap);


    let gradient = {
        '.5': "rgb(32, 85, 153)",
        '.8': "rgb(0, 255, 191)",
        '.95':"rgb(232, 102, 102)"
      };



    var heatmapInstance = h337.create({
      container: document.querySelector('.Heatmap'+auth),
      radius: 8,
      maxOpacity: .5,
      minOpacity: 0,
      blur: .75,
    });
    var data = {
      min: 1,
      max: this.state.playerHeatmapsMaxvalue[index],
      data: heatmap
    };
    heatmapInstance.setData(data);
  }

  kFormatter(num) {
    return Math.abs(num) > 999 ? Math.sign(num)*((Math.abs(num)/1000).toFixed(1)) + 'k' : Math.sign(num)*Math.abs(num)
  }


  calculatePlayerStats(player){
    player.cleansheetrate = parseInt(Math.floor((player.gkcleansheetcount/player.gkmatchcount).toFixed(2) * 100) || 0);
    player.winrate1v1 = parseInt(Math.floor((player.total1v1wincount/player.total1v1count).toFixed(2) * 100) || 0);
    player.distancepergame = parseInt(player.totaldistancecovered / player.stats2matchcount || 0);
    player.concededpergame = (player.gkconcededcount / player.gkmatchcount || 0);

    player.winrate = parseInt(Math.floor((player.totalwincount/player.totalmatchcount).toFixed(2) * 100) || 0);
    player.lossrate = parseInt(Math.floor((player.totallosscount/player.totalmatchcount).toFixed(2) * 100) || 0);
    player.goalspergame = (player.totalscorecount / player.totalmatchcount || 0);
    player.assistspergame = (player.totalassistcount / player.totalmatchcount || 0);
    player.scorerpointspergame = (parseFloat(player.goalspergame) + parseFloat(player.assistspergame));

    //TEMP FIX UNTIL SERVERSIDE UPDATE
    //player.hattrickcount -= parseInt(player.unstoppablecount);
    //player.unstoppablecount -= parseInt(player.dominatingcount);
    //FIXED

    let cleansheetmultiplier = Math.max(0, 1.0 - (player.concededpergame - 0.7));
    let cleansheetaddition = Math.min(100, player.cleansheetrate + (50 * cleansheetmultiplier));

    let concededmultiplier = Math.min(1.0, Math.max(0,player.concededpergame-0.5) / 4.5);
    let concededmax = Math.min(1, player.concededpergame);
    let defenserating = 100 - (concededmax * (100-cleansheetaddition) + (cleansheetaddition*concededmultiplier));
    player.defenserating = parseInt(defenserating);

    if(player.gkmatchcount < 10){
      player.defenserating = 0;
    }
    let offensemultiplier = player.scorerpointspergame+0.5;
    let offenseadditionmultiplier = Math.min(1.0,Math.max(0, offensemultiplier - 1.0));
    offensemultiplier = Math.min(1, offensemultiplier);

    let weightedscorerpoints = 0.95 * Math.min(1,player.scorerpointspergame/0.95);
    weightedscorerpoints = weightedscorerpoints + (Math.max(0,player.scorerpointspergame - 1.0)*0.05);

    let offenserating = weightedscorerpoints * offensemultiplier;
    let offenseaddition = (1.0 - offenserating) * offenseadditionmultiplier;
    offenserating = offenserating + offenseaddition;
    player.offenserating = parseInt(offenserating*100);

    if(player.totalmatchcount < 30){
      player.offenserating = 0;
    }

    let gkpercentage = player.gkmatchcount / player.stats2matchcount;
    if(player.gkmatchcount == 0){gkpercentage = 0;}
    gkpercentage = gkpercentage / 0.8;

    let winrateweighting = ((player.winrate/100) / 0.70)/2 + 0.6;
    let weighteddefenserating = player.defenserating * gkpercentage;
    if(player.defenserating == 0){weighteddefenserating = 0;}
    let weightedoffenserating = player.offenserating * (1-gkpercentage);
    player.rating = parseInt(Math.min(100,(weighteddefenserating + weightedoffenserating) * winrateweighting));
    if(player.defenserating == 0){
      player.rating = Math.max(0,player.rating - 5);
    }
    //console.log(player.lastname + ": " + player.stats2matchcount + " - " + gkpercentage + " - " + player.defenserating*gkpercentage + " - " + winrateweighting);
  }

