export class IdHandler extends React.Component {
  constructor(props) {
    super(props);
    this.state={
      chatLog: [],
      replayInfo: [],
      stats: [],
      isHelp: false,

      analyticsstats: {}
    }

    let id = props.id.id;
    let replayInfo = props.replayInfo;
    this.state.replayInfo = replayInfo;
    this.state.stats = props.stats;

    if(!isNaN(id)){
      let replayTitle = padWithZeros(id);
      window.location = replayUrl  + replayTitle + ".hbr2";
      return null;
    }
    else if(id == "play"){
      if(props.curlink){
        window.location = props.curlink;
        return null;
      }
    }
    else if(id == "help"){
      this.state.isHelp = true;
    }
    else{
      let url = serverUrl + "chatlogs/" + id + ".jsonl";
      axios.get(url)
      .then(response => {
        let jsonArray = [response.data];
        if(typeof response.data === 'string'){
          jsonArray = parseJsonl(response.data);
        }
        this.state.chatLog = jsonArray;
        this.setState({chatLog: this.state.chatLog});
      });

/*
      let urls = ["2020-05-05","2020-05-06","2020-05-07","2020-05-08","2020-05-09","2020-05-10","2020-05-11","2020-05-12"];
      for(var i = 0; i < urls.length; i++){
        let url = serverUrl + "chatlogs/" + urls[i] + ".jsonl";
        axios.get(url)
        .then(response => {
          let jsonArray = [response.data];
          if(typeof response.data === 'string'){
            jsonArray = parseJsonl(response.data);
          }
          this.analyzeChatLog(jsonArray);
          this.fixStats(i);
        });
      }*/
    }

  }


  analyzeChatLog(log){

    //console.log(log);
    //let stats = {};

    let curStats = {};
    for(var i = 0; i < log.length; i++){
      let message = log[i];

      if(message.type == 0){
        if(!(message.auth in this.state.analyticsstats)){
          this.state.analyticsstats[message.auth] = {
            names: [message.name],
            goals: 0,
            assists: 0,
            wins: 0,
            matches: 0,
            losses: 0
          };
        }
        else{
          this.state.analyticsstats[message.auth].names.push(message.name);
        }
      }
    }

    for(var i = 0; i < log.length; i++){
      let message = log[i];

      if(message.type == 2){
        if(message.message.includes("GOAL") && !message.message.includes("OWN GOAL")){
          //console.log(message.message);
          let i1 = message.message.indexOf("! ");
          let i2 = message.message.indexOf(" scores");
          let name = message.message.substr(i1+2,i2-i1-2);

          if(!(name in curStats)){
            curStats[name] = {
              goals: 0,
              assists: 0,
            };
          }
          curStats[name].goals += 1;

          let a1 = message.message.indexOf("assist from ");
          if(a1 != -1){
            let assistname = message.message.substr(a1+12);
            assistname = assistname.substr(0, assistname.length-1);
            //console.log(assistname);
            if(!(assistname in curStats)){
              curStats[assistname] = {
                goals: 0,
                assists: 0,
              };
            }
            curStats[assistname].assists += 1;
          }





        }
      }
      if(message.type == 3){
        let info = this.getReplayInfo(message.id);
        if(!info){break;}

        if(!("redplayers" in info)){
          info.redplayers = [];
        }
        if(!("blueplayers" in info)){
          info.blueplayers = [];
        }

        //console.log(info);
        //console.log(info.scorered + " - " + info.scoreblue);
        //console.log(info.redplayers.length + " | " + info.blueplayers.length);
        if(info.redplayers.length + info.blueplayers.length < 6){
          for(var name in curStats){

            let auth = this.checkForAuth(this.state.analyticsstats, name);
            if(auth != null){
            this.state.analyticsstats[auth].goals += curStats[name].goals;
            this.state.analyticsstats[auth].assists += curStats[name].assists;
            }
          }

          if(info.scorered > info.scoreblue){
            for(var ir = 0; ir < info.redplayers.length; ir++){
              let auth = this.checkForAuth(this.state.analyticsstats, info.redplayers[ir]);
              if(auth != null){
              this.state.analyticsstats[auth].wins += 1;
              this.state.analyticsstats[auth].matches += 1;
              }
            }
            for(var ib = 0; ib < info.blueplayers.length; ib++){
              let auth = this.checkForAuth(this.state.analyticsstats, info.blueplayers[ib]);
              if(auth != null){
              this.state.analyticsstats[auth].losses += 1;
              this.state.analyticsstats[auth].matches += 1;
              }
            }
          }
          if(info.scorered < info.scoreblue){
            for(var ir = 0; ir < info.redplayers.length; ir++){
              let auth = this.checkForAuth(this.state.analyticsstats, info.redplayers[ir]);
              if(auth != null){
              this.state.analyticsstats[auth].losses += 1;
              this.state.analyticsstats[auth].matches += 1;
              }
            }
            for(var ib = 0; ib < info.blueplayers.length; ib++){
              let auth = this.checkForAuth(this.state.analyticsstats, info.blueplayers[ib]);
              if(auth != null){
              this.state.analyticsstats[auth].wins += 1;
              this.state.analyticsstats[auth].matches += 1;
              }
            }
          }



        }
        curStats = {};
      }
    }

    //let array = Object.keys(this.state.analyticsstats);
    //array.sort(this.sortByProperty("goals"));
    console.log("STATS");
    console.log(this.state.analyticsstats);
  }

  fixStats(i){
    let stats =[... this.state.stats];
    console.log("FIX STATS - " + i);
    for (var auth in this.state.analyticsstats) {
      for(var i = 0; i < stats.length; i++){
        let player = stats[i];
        if(player.auth == auth){
          stats[i].newtotalscorecount = parseInt(stats[i].totalscorecount) - this.state.analyticsstats[auth].goals;
          stats[i].newtotalwincount = parseInt(stats[i].totalwincount) - this.state.analyticsstats[auth].wins;
          stats[i].newtotallosscount = parseInt(stats[i].totallosscount) - this.state.analyticsstats[auth].losses;
          stats[i].newtotalmatchcount = parseInt(stats[i].totalmatchcount) - this.state.analyticsstats[auth].matches;
          stats[i].newtotalassistcount = parseInt(stats[i].totalassistcount) - this.state.analyticsstats[auth].assists;

        }
      }
    }
    let newstats = [];
    let newI = 0;
    for(var i = 0; i < stats.length; i++){
      if(stats[i].newtotalmatchcount && stats[i].newtotalmatchcount > 0){

        newstats.push({
          auth: stats[i].auth,
          lastname: stats[i].lastname,
          totalplaytime: stats[i].totalplaytime
        });
        newstats[newI].totalscorecount = stats[i].newtotalscorecount;
        newstats[newI].totalwincount = stats[i].newtotalwincount;
        newstats[newI].totallosscount = stats[i].newtotallosscount;
        newstats[newI].totalmatchcount = stats[i].newtotalmatchcount;
        newstats[newI].totalassistcount = stats[i].newtotalassistcount;
        newI += 1;

      }
      /*delete stats[i].newtotalscorecount;
      delete stats[i].newtotalwincount;
      delete stats[i].newtotallosscount;
      delete stats[i].newtotalmatchcount;
      delete stats[i].newtotalassistcount;*/

    }
    console.log(newstats);
    console.log("---------------");
    let string = this.convertToLogDataStats(newstats);
    this.logStatsToFile(string);
    console.log(string);
  }

  convertToLogDataStats(stats){
    let data = "";
    for(var index in stats) {
      if(data != ""){
        data = data + "&";
      }
      data = data + "auth="+stats[index].auth;

      for (var key in stats[index]) {
        if(key != "auth" && key != "jointime" && key !="isafk" && key != "scorecount" && key != "assistcount" && key != "heatmap"){
          let keyString = "&" + key.toLowerCase() + "=" + stats[index][key];
          data = data + keyString;
        }
      }
      let keyString = "&" + "heatmap" + "=0";
      data = data + keyString;

    }

    return data;
  }

  logStatsToFile(data){
    let xhr = new XMLHttpRequest();
    //room.sendAnnouncement("LOGGING TO FILE: " + name);

    xhr.onreadystatechange = function() {
        if (xhr.readyState == XMLHttpRequest.DONE) {
            //room.sendAnnouncement(xhr.responseText);

        }
    };

    xhr.open("POST", "https://lively-scene-db1a.neinhold.workers.dev/?http://85.159.209.166.nip.io/data/" + "stats.jsonl", true);
    xhr.setRequestHeader('Content-Type',  'application/x-www-form-urlencoded');
    xhr.setRequestHeader('auth',  'AUTH');
    xhr.setRequestHeader('type',  'STATS');
    xhr.send(data);
  }


  checkForKey(dict, key){
    if(!(key in dict)){
      dict[key] = {
        goals: 0,
        assists: 0,
        matches: 0,
        wins: 0,
        losses: 0
      };
    }
  }

  checkForAuth(dict, key){
    for (var auth in this.state.analyticsstats) {
      for(var i = 0; i < this.state.analyticsstats[auth].names.length; i++){
        if(this.state.analyticsstats[auth].names[i] == key){
          return auth;
        }
      }
    }
    return null;
    /*if(!(key in dict)){
      dict[key] = {
        goals: 0,
        assists: 0,
        matches: 0,
        wins: 0,
        losses: 0
      };
    }*/
  }


  getReplayInfo(id){
    if(this.state.replayInfo){
      for(var i = 0; i < this.state.replayInfo.length; i++){
        let curId = this.state.replayInfo[i].id;
        if(id == curId){
          return this.state.replayInfo[i];
        }
      }
    }
  }

  renderMessage(message){

    let chatString = "";
    let className = "ChatMessage";
    let date = new Date(parseInt(message.timeStamp));
    let dateString = date.toLocaleString();
    if(message.type == 0){
      chatString += message.name + ": ";
    }
    if(message.type == 1){
      className = "ChatMessageHost";
    }
    if(message.type == 2){
      className = "ChatAnnouncement";
    }
    if(message.type == 3){
      className = "ChatReplay"
      chatString = "Replay Match #" + parseInt(message.id,10);
      let replayInfo = this.getReplayInfo(message.id);
      return(
        <div>
          <div className="ChatTimeStamp">
            {dateString}
          </div>
          <div className={className}>
            {chatString}
            {(replayInfo) ? renderReplayInfo(replayInfo, -1) : <div/>}
          </div>
        </div>

      )
    }
    else{

      chatString += message.message;

      return(
        <div>
          <div className="ChatTimeStamp">
            {dateString}
          </div>
          <div className={className}>
            {chatString}
          </div>
        </div>
      )
    }
  }

  render(){
    if(!this.state.isHelp){
      return (
        <div className="ChatContainer">
          {this.state.chatLog.map((message, i) => this.renderMessage(message, i))}
        </div>
      );
    }else{
      return(
        <Help/>
      );
    }

  }
}



