export function  parseJsonl(jsonl){
    let rawLines = jsonl;
    let lineArray = rawLines.split('\n');
    lineArray = lineArray.slice(0, lineArray.length-1);

    let jsonArray = [];
    for(var i = 0; i < lineArray.length; i++){
      let json = JSON.parse(lineArray[i]);
      jsonArray.push(json);
    }
    return jsonArray;
  }

  export function sToString(s){
    var totalSeconds =  s;
    var minutes = Math.floor(totalSeconds / 60);
    var seconds = totalSeconds - (minutes * 60);
    var timeString = minutes + "m" + parseInt(seconds) + "s";
    return timeString;
  }

  export function padWithZeros(num) {
    var s = "000000000" + num;
    return s.substr(s.length-8);
  }

  export function renderModifiers(replay){
      if(replay.modifiers != "No modifiers active"){
        return(
          <div className="SubTitle">
          {replay.modifiers}
          </div>
        )
      }
  }


  export function renderReplayInfo(replay,i){

    return(
      <ReplayInfo replay={replay} i={i}/>
    )

  }


  export class ReplayInfo extends React.Component {
    constructor(props) {
      super(props);
      this.state={
        curTab: 0,
        heatmaps: null,
        blueHeatMap: null,
        maxValue: 1,
        isLoaded: false,
      }




    }

    loadHeatmap(){
      //let url = "https://lively-scene-db1a.neinhold.workers.dev/?http://15.236.60.119.nip.io/heatmaps/replays/00000001.json";
      let url = serverUrl + "heatmaps/replays/" + this.props.replay.id + ".json";
      axios.get(url)
      .then(response => {
        let jsonArray = [response.data];

        jsonArray = jsonArray[0];


        let data = [];
        let maxValue = 1;
        for(var i = 0; i < jsonArray.length; i++){
          data.push({
            name: jsonArray[i].name,
            team: jsonArray[i].team,
            heatmap: [],
            visible: true
          });
          for(var x = 0; x < jsonArray[i].heatmap.length; x++){
            for(var y = 0; y < jsonArray[i].heatmap[x].length; y++){
              let xCoord = x * 6.5;
              let yCoord = y * 3.7666;
              if(jsonArray[i].team == 2){
                xCoord = (40-x) * 6.5;
                yCoord = (30-y) * 3.7666;
              }
              let dataPoint = {
                x: xCoord,
                y: yCoord,
                value: jsonArray[i].heatmap[x][y]+1
                //value: Math.min(jsonArray[i].heatmap[x][y] +1, 3)
              }
              data[i].heatmap.push(dataPoint);
              if(dataPoint.value > maxValue){
                maxValue = dataPoint.value;
              }
            }
          }
        }
        this.state.heatmaps = data;
        this.state.maxValue = maxValue;
        this.setState({heatmaps: this.state.heatmaps});
        console.log(data);


      });
    }

    renderTabs(tabs){
      let tabClasses = ["Tab", "Tab"];
      tabClasses[this.state.curTab] += " Selected";

      if(tabs){
        return(
          <div>
            <div onClick={() => this.setState({curTab: 0, isLoaded: false})} className={tabClasses[0]}>
              Info
            </div>
            <div onClick={() => this.setState({curTab: 1, isLoaded: false})} className={tabClasses[1]}>
              Heatmap
            </div>
          </div>
        )
      }else{
        return(<div/>)
      }
    }

    generateHeatMap(index){
      let id = this.props.replay.id;
      let heatmap = this.state.heatmaps[index].heatmap;


      let gradient = {
          '.5': "rgb(232, 102, 102)",
          '.8': "rgb(232, 60, 161)",
          '.95':"rgb(255, 0, 234)"
        };
      if(this.state.heatmaps[index].team == 2){
        gradient = {
          '.5': "rgb(32, 85, 153)",
          '.8': "rgb(63, 218, 232)",
          '.95':"rgb(0, 255, 191)"
        }
      }



      var heatmapInstance = h337.create({
        container: document.querySelector('.Heatmap'+id+"_"+index),
        radius: 8,
        maxOpacity: .5,
        minOpacity: 0,
        blur: .75,

        gradient: gradient
      });
      var data = {
        min: 1,
        max: 8,
        data: heatmap
      };
      heatmapInstance.setData(data);
    }

    toggleHeatMap(index){

      let start = index;
      let range = index+1;
      let team = 0;
      if(index == "red" || index == "blue"){
        start = 0;
        range = this.state.heatmaps.length;
        team = 1;
      }
      if(index == "blue"){
        team = 2;
      }

      let curVisibility = null;

      for(var i = start; i < range; i++){
        if(this.state.heatmaps[i].team == team || team == 0){
          let container = document.querySelector('.Heatmap'+this.props.replay.id+"_"+i);
          if(curVisibility == null){
            curVisibility = container.style.visibility;
          }
          if(curVisibility == "hidden"){
            container.style.visibility='visible';
            this.state.heatmaps[i].visible = true;
            this.setState({curTab: this.state.curTab});
          }else{
            container.style.visibility='hidden';
            this.state.heatmaps[i].visible = false;
            this.setState({curTab: this.state.curTab});
          }
        }
      }
    }


    renderHeatmap(heatmap,i){




      return(
        <div className = "HeatmapContainer">
          <div style={{ 'height': 133}} className={"Heatmap" + this.props.replay.id + "_" + i}>
          </div>
        </div>
      )
    }

    renderToggle(team, i){
      let color = "#888888";
      if(this.state.heatmaps[i].visible){
        color = "#FFFFFF";
      }

      if(this.state.heatmaps[i].team == team){
        return(
          <div  style={{ 'color':color }}  className="TogglePlayer" onClick={()=>this.toggleHeatMap(i)}>{this.state.heatmaps[i].name}</div>

        )
      }
    }


    render(){
      let containerName = "Container";
      if(this.props.i == -1){
        containerName = "ContainerChat";
      }
      let tabs = false;
      if(this.props.replay.stadium == "Big"){
        tabs = true;
      }

      if(this.state.curTab == 0){
        return(

          <div  className={containerName + " Click"}>
            {this.renderTabs(tabs)}
            <a href={replayUrl + this.props.replay.id + ".hbr2"} target="_blank">
            <div className="SmallDigitSmall">
            {parseInt(this.props.replay.id,10)}
            </div>
            <div className="InfoPlainTitle">
            {sToString(this.props.replay.duration)}
            </div>
            <div className="SubTitleOffset">
            {this.props.replay.stadium}
            </div>
            {renderModifiers(this.props.replay)}
            <div className="PlayerStatsGames">
              <div className="InfoBoxWide">
                <div className="InfoPlainTitle">
                Red
                </div>
                <div className="SmallDigit">
                {this.props.replay.scorered}
                </div>
                <hr/>
              </div>
              <div className="InfoBoxWide">
                <div className="InfoPlainTitle">
                Blue
                </div>
                <div className="SmallDigit">
                {this.props.replay.scoreblue}
                </div>
                <hr/>
              </div>
              <div className="PlayerStatsGames">
                <div className="InfoBoxWide">
                  <div className="SubTitle">
                  {this.props.replay.redplayers.map(function(player, i){
                    return <div className="SubTitleOffset">
                    {player}
                    </div>
                  })}
                  </div>
                </div>
                <div className="InfoBoxWide">
                  <div className="SubTitle">
                  {this.props.replay.blueplayers.map(function(player, i){
                    return <div className="SubTitleOffset">
                    {player}
                    </div>
                  })}
                  </div>
                </div>
              </div>
            </div>
            </a>
          </div>

        )
      }else{
        if(this.state.heatmaps == null){

          this.loadHeatmap();
        }else if(!this.state.isLoaded){
          let self = this;
          setTimeout(function() {
            for(var i = 0; i < self.state.heatmaps.length; i++){
              self.generateHeatMap(i);
            }
            self.state.isLoaded = true;
          }, 500);

        }

        if(this.state.heatmaps == null){

          return(
            <div  className={containerName + " Click"}>

              {this.renderTabs(tabs)}
              <div className={"Subtitle"}>
                No Heatmap available for this match.
              </div>

            </div>
          )
        }else{

          return(
            <div  className={containerName + " Click"}>

              {this.renderTabs(tabs)}
              <div className={"Heatmap"}>
                <img className="Stadium" src={hax_big}/>
                {this.state.heatmaps.map((heatmap, i) => this.renderHeatmap(heatmap, i))}

              </div>

              <div className="Toggle" onClick={()=>this.toggleHeatMap("red")}>Toggle Red</div>
              <div className="Toggle" onClick={()=>this.toggleHeatMap("blue")}>Toggle Blue</div>
              <div>

              <div className="ToggleContainer">
              {this.state.heatmaps.map((heatmap, i) => this.renderToggle(1, i))}
              </div>

              <div className="ToggleContainer">
              {this.state.heatmaps.map((heatmap, i) => this.renderToggle(2, i))}
              </div>
              </div>

            </div>
          )
        }
      }

    }
  }

  export default withRouter(App);
